.list-strong {
	font-size: 0; }
.list-strong li {
	display: block; }
.list-strong li strong {
	@include f(16px, 700, 14px);
	font-family: $familySecondary;
	color: $gray;
	display: inline-block;
	vertical-align: top;
	min-width: 30px;
	margin-right: 12px; }

.list_graph {
	font-size: 0;
	display: inline-block;
	vertical-align: top;
	li p {
		@include f(14px, 400, 1);
		font-family: $familySecondary; }
	&-vertical {
		margin-top: -5px; }
	&-vertical li:not(:last-child) {
		margin-bottom: 20px; }
	&-vertical li p {
		display: block; }
	&-horizontal {
		margin-left: 12px; }
	&-horizontal li {
		display: inline-block;
		vertical-align: top;
		&:not(:first-child) {
			margin-left: 70px; } }
	&-horizontal li p {
		display: inline-block; } }

.list-bordered {
	&_item {
		display: block;
		&:first-child {
			border-top: 1px solid #DDDDDD; } }
	&_item {
		border-bottom: 1px solid #DDDDDD; }
	&_item p {
		padding: 25px 20px;
		color: $gray;
		display: inline-block;
		vertical-align: middle;
		@include f(16px, 400, 0.875);
		font-family: $familySecondary;
		width: 50%;
		&.text-bolder {
			@include f(16px, 700, 0.875);
			font-family: $familyPrimary; } } }
