.cards {
	padding: 40px 0 43px;
	background: #EEEEEE;
	&__title {
		margin-bottom: 22px;
		text-align: center; }
	&__row {
		margin: 0 -8px;
		@include clr; }
	&__col {
		float: left;
		padding: 8px;
		width: 33.33%;
		&.cards__half {
			width: 50%;
			padding: 10px;
			.card__link {
				margin-bottom: 0; } } } }
