.form-group {
	font-size: 0;
	&__label {
		display: block;
		@include f(14px, 700, 1);
		font-family: $familySecondary;
		margin-bottom: 8px; }
	&__input {
		border: 1px solid #cccccc;
		padding: 8px 16px;
		border-radius: 0;
		background-color: $white;
		width: 100%;
		color: #6F6F6F;
		@include f(16px, 400, 14px);
		@include placeholder-color(#6F6F6F);
		&.input_bordered {
			border: 1px solid #cccccc; } }
	&__row {
		font-size: 0;
		margin: 0 -5px;
		&.form-group_navigate {
			margin: 0;
			border-bottom: 2px solid #E6E6E6;
			.form-group__input {
				max-width: 120px;
				vertical-align: top; } } }
	&__col {
		width: 50%;
		display: inline-block;
		vertical-align: top;
		padding: 0 5px; } }


.input {
	&__btn {
		width: 34px;
		height: 36px;
		background-color: $white;
		transition: background-color .30s ease-in-out, border-color .30s ease-in-out;
		position: relative;
		font-size: 16px;
		border: 1px solid #cccccc;
		cursor: pointer;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 100%;
			margin-top: 1px;
			left: -1px;
			width: 34px;
			height: 2px;
			background-color: #E6E6E6;
			transition: background-color .30s; }
		@include hover {
			background-color: #E6E6E6;
			&:after {
				background-color: $gray; }
			.icon, i {
				fill: $gray;
				color: $gray; } }
		.icon, i {
			color: #6F6F6F;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate3d(-50%, -50%, 0);
			transition: fill .30s ease-in-out;
			fill: #6F6F6F; } } }
