@mixin col($name) {
	&[data-column*="#{$name}"] {
		@content; } }

.table {
	display: table;
	table-layout: fixed;
	width: 100%;
	&__thead {
		display: table-header-group;
		background: #F5F5F5;
		&.thead__white {
			background-color: $white; } }
	&__tfooter {
		display: table-footer-group; }
	&__tbody {
		display: table-row-group; }
	&__tr {
		display: table-row;
		&.is-checked {
			box-shadow: 0 0 0 3px $blue; } }
	&__td,
	&__th {
		display: table-cell;
		vertical-align: top;
		@include col('product-name') {
			width: 38%; }
		@include col('fundserv') {
			width: 13%; }
		@include col('available-until') {
			width: 17%; }
		@include col('term') {
			width: 12%; }
		@include col('compare') {
			width: 20%; }
		@include col('event-date') {
			width: 13%; }
		@include col('event-type') {
			width: 22%; }
		@include col('denotation') {
			width: 25%; }
		@include col('code') {
			width: 12%; }
		@include col('status') {
			width: 11%; } }
	&__th {
		padding: 18px 18px 17px;
		border-top: 1px solid $lightGrayDarker;
		border-bottom: 1px solid $lightGrayDarker;
		font-size: 0; }
	&__td {
		padding: 14px 18px;
		border-bottom: 1px solid $lightGrayDarker; }
	&__arrow {
		margin-top: 3px;
		margin-left: 10px;
		font-size: 16px; }
	&__heading {
		@include f(16px, 700, 20px); }
	&__help {
		margin-top: 3px;
		margin-left: 10px; }
	&__heading,
	&__arrow,
	&__help {
		display: inline-block;
		vertical-align: top; }
	.watchlist__controls {
		@include clr;
		.checkbox {
			margin-top: 0;
			margin-right: 20px;
			float: left; }
		p {
			overflow: hidden; } }
	.checkbox {
		margin-top: 4px; }
	&.table_compares .table {
		&__thead {
			background: transparent;
			.table__td {
				padding: 0 20px 38px; } }
		&__tbody {
			background: transparent;
			.table {
				&__td {
					@include f(16px, 400, 30px);
					font-family: $familySecondary; } } }
		&__td {
			padding: 17px 18px;
			width: 25%;
			&:first-child {
				font-family: $familyPrimary;
				@include f(20px, 700, 30px); } }
		&__tfooter .table {
			&__td {
				border-bottom: none; } } } }
